      left: 30%
  }

  25% {
      opacity: 1;
      left: 26%;
      top: 0%
  }

  50% {
      opacity: 1;
      left: 23%;
      top: -22%
  }

  75% {
      opacity: 1;
      left: 19%;
      top: -18%
  }

  100% {
      opacity: 1;
      left: 14%;
      top: 28%
  }
}

